<template>
  <div class="forget">
    <div class="login-box">  
       <div class="logo">
         <img src="../assets/images/snipaste1.png" alt="" class="auto-img">
          <div class="close-box" @click="gohome">
            <svg
              t="1596705973022"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="2063"
              width="200"
              height="200"
            >
              <path
                d="M298.666667 586.666667a74.666667 74.666667 0 1 1 0-149.333334 74.666667 74.666667 0 0 1 0 149.333334z m213.333333 0a74.666667 74.666667 0 1 1 0-149.333334 74.666667 74.666667 0 0 1 0 149.333334z m213.333333 0a74.666667 74.666667 0 1 1 0-149.333334 74.666667 74.666667 0 0 1 0 149.333334z"
                p-id="2064"
                fill="#bfbfbf"
              />
            </svg>
          </div>
       </div>

      <div class="login-form">
        <div class="login-form-box">
          <van-form class="yan">
            <van-field  placeholder="请输入手机号" v-model="userforgetinfo.phone" />
            <van-field
              :type="ispassword ? 'password':'text'"
              placeholder="请输入密码"
              v-model="userforgetinfo.password"
              :right-icon="ispassword ? 'closed-eye' : 'eye-o'"
              @click-right-icon="password"
            />
            <van-field  placeholder="请输入邮箱账号" v-model="userforgetinfo.email" />
            <van-field  placeholder="请输入邮箱验证码" v-model="userforgetinfo.validCode" />
            <div class="yanzheng" click="send">发送验证码</div>
            <div class="clearfix forgot">
              <span class="fr" click="gologin">已有账号,立即登录</span>
            </div>
            <div class="login-btn">
              <van-button round block type="info" native-type="submit" @click="submit" color="#5f56ff">提交</van-button>
            </div>
          </van-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/forget.less";
import {validForm} from '../assets/js/validForm';
import {utils} from '../assets/js/utils';

export default {
  data() {
    return {
      userforgetinfo: {
        phone: "",
        password: "",
        email: "",
        validCode: "",
      },
      ispassword: true,
      text: '发送验证码',
      isSend: false
    };
  },
  methods: {
    //跳到home页面
    gohome() {
     this.$router.push({name:'Home'})
    },
    // 回到登录界面
    gologin(){
        this.$router.push({name:"Login"})
    },
    // 密码显示隐藏
    password() {
      this.ispassword = !this.ispassword;
    },
    // 弹出层
    tangchu() {
      this.isRegister = true;
    },

     //发送验证码
    send() {
      let time = 5;
      this.text = time + 's后重新发送';
      this.isSend = true;

      let timer = setInterval(() => {
        if (time == 0) {
          clearInterval(timer);
          this.isSend = false;
          this.text = '发送验证码';
        } else {
          time--;
          this.text = time + 's后重新发送';
        }
      }, 1000)

      //发起获取邮箱验证码请求
      //验证通过
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0,
        loadingType: 'spinner'
      })

      //参数序列化
      let data = utils.queryString({
        email: this.userforgetinfo.email,
        appkey: this.appkey
      });

      this.axios({
        method: 'POST',
        url: '/emailValidCode',
        data
      }).then(result => {
        this.$toast.clear();
        

        this.$notify({
          type: 'warning', 
          message: result.data.msg
        })
      }).catch(err => {
        this.$toast.clear();
        
      })
    },

    //修改密码
    updatepassword() {
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0,
        loadingType: 'spinner'
      })

      //参数序列化
      let data = utils.queryString({
        phone: this.userforgetinfo.phone,
        password: this.userforgetinfo.password,
        appkey: this.appkey
      });

      this.axios({
        method: 'POST',
        url: '/retrievePassword',
        data
      }).then(result => {
        this.$toast.clear();
        

        if (result.data.code == 'L001') {
          this.$router.push({name: 'Login'});
        } else {
          this.$toast(result.data.msg);
        }

      }).catch(err => {
        this.$toast.clear();
        

      })
    },
    //提交
    submit() {
      //验证修改密码表单
      let o = {
        phone: {
          value: this.userforgetinfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: '手机号格式不正确'
        },
        password: {
          value: this.userforgetinfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: '密码支持字母数字_组合'
        },
        email: {
          value: this.userforgetinfo.email,
          reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          errorMsg: '邮箱格式不正确'
        },
        validCode: {
          value: this.userforgetinfo.validCode,
          reg: /^\d{6}$/,
          errorMsg: '验证为6位数字'
        }
      };

      if (!validForm.valid(o)) {
        //验证不通过
        return;
      }

      //发起忘记密码请求
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0,
        loadingType: 'spinner'
      })

      //参数序列化
      let data = utils.queryString({
        validCode: this.userforgetinfo.validCode,
        appkey: this.appkey
      });

      this.axios({
        method: 'POST',
        url: '/checkValidCode',
        data
      }).then(result => {
        this.$toast.clear();
        

        if (result.data.code == 'K001') {
          //验证成功
          this.updatepassword();

        } else {
          this.$notify({
            type: 'warning', 
            message: result.data.msg
          })
        }

        
      }).catch(err => {
        this.$toast.clear();
        

      })

    },

    

  },
};
</script>

<style lang="less" scoped>
</style>